<template>
  <page>
    <router-view  class="router-view-class"></router-view>
    <header-title v-bind="headerOptions" @leftClick="gg"/>
    <div class="swipe-wrapper">
      <mt-swipe :auto="4000">
        <mt-swipe-item>
          <div style="background-color:blue;height:100%"></div>
        </mt-swipe-item>
        <mt-swipe-item>
          <div style="background-color:#f40;height:100%"></div>
        </mt-swipe-item>
        <mt-swipe-item>
          <div style="background-color:#f4f;height:100%"></div>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="announcement">
      <div>
        <i class="icon icon-home"></i>
      </div>
      <div @click="jump('details')">
        <span>{{'23423ju42hik3u4h2sdfj43i3333333333333333333334f'}}</span>
      </div>
    </div>
    <buttoms class="buttoms" :iconObjectList="iconObjectList"/>
  </page>
</template>

<script>
import Buttoms from '../../components/buttoms'

export default {
  components: {
    Buttoms
  },
  data () {
    return {
      iconObjectList: [
        { icon: 'icon-club', title: '俱乐部', color: '#fe9b1c' },
        { icon: 'icon-team', title: '组队', color: '#3ba29f' },
        { icon: 'icon-task', title: '任务', color: '#3eaffd', path: '/home/task' },
        { icon: 'icon-school', title: '商学院', color: '#c57ffc' }
      ],
      pictureSource: '',
      destinationType: ''
    }
  },
  computed: {
    headerOptions () {
      return { title: '首页', backIcon: 'icon-home' }
    }
  },
  methods: {
    handleClick: function () {
      // 扫一扫方法
      cordova.plugins.barcodeScanner.scan(
        function (result) {
          alert('We got a barcode\n' +
            'Result: ' + result.text + '\n' +
            'Format: ' + result.format + '\n' +
            'Cancelled: ' + result.cancelled)
        },
        function (error) {
          alert(error)
        }
      )
    },
    jump (path) {
      this.$router.push({path: `/home/${path}`})
    }
  },
  created () {
    document.addEventListener('deviceready', this.onDeviceReady, false)
  }
}
</script>
<style lang="scss" scoped>
.announcement {
  height: 30px;
  display: flex;
  color: #fff;
  background-color: $item-color;
  align-items: center;
  margin-bottom: 1px;
  div:nth-child(1) {
    width: 50px;
    min-width:50px;
    border-right: 1px solid $background-color;
    text-align: center;
  }
  div:nth-child(2) {
    font-size: 14px;
    padding:0 20px 0 20px;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
.buttoms {
  background-color: $item-color;
}
.progress-background {
  background-color: #f40;
}
.progress-bar {
  background-color: #fdf;
}
.swipe-wrapper {
  height: 160px;
  .item-wrapper {
    height: 300px;
    width: 100%;
    background-color: #f40;
  }
}
</style>



